.container {
  margin: 0 auto;
  padding: 0 45px;
}

@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

// $media-breakpoint-xl = 1199px
@media (max-width: $media-breakpoint-xl) {
  .container {
    padding: 0 31px;
  }
}

// $media-breakpoint-lg = 991px
@media (max-width: $media-breakpoint-lg) {
  .container {
    padding: 0 39px;
  }
}

// $media-breakpoint-md = 767px
@media (max-width: $media-breakpoint-md) {
  .container {
    padding: 0 33px;
  }
}

// $media-breakpoint-sm = 575px
@media (max-width: $media-breakpoint-sm) {
  .container {
    padding: 0 24px;
  }
}
