.events {
  margin: 0;

  background-color: $color-blue;
}

.events__wrapper {
  margin: 0;
  padding: 136px 0 140px;
}

.events__title {
  position: relative;

  margin: 0;
  margin-bottom: 60px;
  padding-left: 117px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $white;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 16px;

    width: 100px;
    height: 16px;

    background: url("../img/icon__decor-heading.svg") no-repeat center center;
  }
}

.events__list {
  margin: 0;

  font-size: $baseFontSize;
  line-height: $baseLineHeght + 3;
  color: $color-darker-gray;
}

.events__item {
  position: relative;

  width: 540px;

  margin-right: 30px;

  img {
    display: block;
  }

  p {
    position: absolute;
    right: 36px;
    bottom: 36px;

    margin: 0;
    padding: 16px 25px;
    max-width: 280px;

    background-color: $white;
  }
}

// $media-breakpoint-xl = 1199px
@media (max-width: $media-breakpoint-xl) {
  .events__wrapper {
    padding-bottom: 81px;
  }

  .events__item {
    width: 450px;

    p {
      right: 50px;
    }
  }
}

// $media-breakpoint-lg = 991px
@media (max-width: $media-breakpoint-lg) {
  .events__wrapper {
    padding: 70px 0;
  }

  .events__item {
    margin: 0 auto;
  }
}

// $media-breakpoint-md = 767px
@media (max-width: $media-breakpoint-md) {
  .events__title {
    max-width: 450px;
  }
}

// $media-breakpoint-sm = 575px
@media (max-width: $media-breakpoint-sm) {
  .events {
    .container {
      padding: 0;
    }
  }

  .events__wrapper {
    padding: 13px 0 10px;
  }

  .events__title {
    margin-bottom: 13px;
    padding-left: 24px;

    &::before {
      content: none;
    }
  }

  .events__item {
    padding-bottom: 100px;
    width: auto;

    p {
      top: 72%;
      right: 0;
      bottom: auto;
      left: 0;

      margin: 0 13px;
      max-width: none;
    }
  }
}

// $media-breakpoint-xs = 360px
@media (max-width: $media-breakpoint-xs) {
  .events__item {
    padding-bottom: 100px;
    width: auto;

    p {
      top: 63%;
      bottom: auto;
    }
  }
}
