.main-nav {
  height: 100%;
}

.main-nav__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  height: 100%;
}

.main-nav__toggle {
  display: none;
}

.main-nav__list {
  flex-grow: 1;
}

@media (min-width: 1200px) {
  .main-nav {
    margin-right: auto;
    margin-left: auto;
    padding: 0 45px;
    width: 1110px;
  }
}

@media (max-width: $media-breakpoint-lg) {
  .main-nav {
    &--closed {
      .main-nav__toggle {
        &::before {
          content: "";

          position: absolute;
          top: 23px;
          left: 20px;

          width: 30px;
          height: 4px;

          background-color: $color-blue;
          box-shadow: 0 10px 0 0 $color-blue, 0 20px 0 0 $color-blue;
        }
      }

      .main-nav__wrapper {
        display: none;
      }
    }

    &--opened {
      .main-nav__toggle {
        &::before,
        &::after {
          content: "";

          position: absolute;
          top: 33px;
          left: 20px;

          width: 30px;
          height: 4px;

          background-color: $color-blue;
        }

        &::before {
          transform: rotate(45deg);
        }

        &::after {
          transform: rotate(-45deg);
        }
      }

      .main-nav__wrapper {
        position: absolute;
        top: 100px;
        left: 0;

        display: block;
        width: 100%;
        height: auto;

        background-color: $color-blue;
      }
    }
  }

  .main-nav__toggle {
    position: absolute;
    top: 15px;
    right: 19px;
    z-index: 12;

    display: block;
    padding: 0;
    width: 70px;
    height: 64px;

    background-color: #ffffff;
    border: none;

    cursor: pointer;

    &::before,
    &::after {
      border-radius: 10px;
    }
  }

  .main-nav--nojs {
    .main-nav__toggle {
      display: none;
    }

    .main-nav__wrapper {
      position: static;

      display: block;
      min-height: 0;
    }
  }

  .main-nav__wrapper {
    padding: 10px 20px 41px 50px;
  }
}

// $media-breakpoint-md = 767px
@media (max-width: $media-breakpoint-md) {
  .main-nav__wrapper {
    padding-bottom: 23px;
    padding-left: 45px;
  }

  .main-nav__toggle {
    right: 13px;
  }
}

// $media-breakpoint-sm = 575px
@media (max-width: $media-breakpoint-sm) {
  .main-nav__toggle {
    right: 10px;

    width: 60px;
  }

  .main-nav--closed {
    .main-nav__toggle {
      &::before {
        left: 15px;
      }
    }
  }

  .main-nav--opened {
    .main-nav__toggle {
      &::before,
      &::after {
        left: 15px;
      }
    }
  }

  .main-nav__wrapper {
    padding-left: 30px;
  }
}
