.projects {
  color: $color-dark-gray;

  background: $color-gray-light;
}

.projects__wrapper {
  margin: 0;
  padding: 60px 0 50px;
}

.projects__title {
  position: relative;

  margin: 0;
  margin-bottom: 23px;
  padding-left: 117px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $color-darker-gray;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 16px;

    width: 100px;
    height: 16px;

    background: url("../img/icon__decor-heading-blue.svg") no-repeat center center;
  }
}

.projects__list {
  display: flex;
  flex-wrap: wrap;

  margin: 0;
  padding: 0;

  font-size: $baseFontSize + 4;
  line-height: $baseLineHeght + 6;

  list-style: none;

  li {
    margin-bottom: 30px;
    margin-right: 30px;

    width: 350px;

    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  img {
    display: block;
  }
}

.projects__text {
  display: flex;
  justify-content: center;
  flex-direction: column;

  margin: 0;
  padding: 8px 18px;
  width: 350px;
  height: 108px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $baseFontSize + 4;
  line-height: $baseLineHeght;
  text-align: center;

  background-color: $white;

  span {
    display: block;

    font-size: $baseFontSize;
  }
}

// $media-breakpoint-xl = 1199px
@media (max-width: $media-breakpoint-xl) {
  .projects__list {
    justify-content: space-around;

    li {
      display: flex;
      flex-direction: column;
      align-items: center;

      margin-right: 0;
      margin-bottom: 48px;
      width: 50%;
    }
  }
}

// $media-breakpoint-lg = 991px
@media (max-width: $media-breakpoint-lg) {
  .projects__wrapper {
    padding: 45px 0;
  }

  .projects__list {
    justify-content: center;

    li {
      display: block;

      margin-bottom: 24px;
      width: 100%;

      box-shadow: 0 2px 14px rgba(0, 0, 0, 0.25);
    }

    img {
      display: none;
    }
  }

  .projects__text {
    width: 100%;
  }
}

// $media-breakpoint-md = 767px
@media (max-width: $media-breakpoint-md) {
  .projects {
    .container {
      padding: 0;
    }
  }
}

// $media-breakpoint-sm = 575px
@media (max-width: $media-breakpoint-sm) {
  .projects__title {
    padding-left: 24px;

    font-size: $baseFontSize + 4;
    line-height: $baseLineHeght + 8;

    &::before {
      content: none;
    }
  }

  .projects__text {
    padding: 8px 12px;
  }
}
