.owl-nav {
  position: absolute;

  top: -121px;
  right: 8px;

  button {
    width: 55px;
    height: 55px;
  }

  span {
    color: transparent;
  }

  .owl-prev {
    margin-right: 22px;
  }
}

// $media-breakpoint-lg = 991px
@media (max-width: $media-breakpoint-lg) {
  .owl-nav {
    position: static;

    .owl-prev {
      position: absolute;
      top: 50%;
      left: 8px;

      margin-top: -28px;
    }

    .owl-next {
      position: absolute;
      top: 50%;
      right: 8px;

      margin-top: -28px;
    }
  }
}

// $media-breakpoint-sm = 575px
@media (max-width: $media-breakpoint-sm) {
  .owl-nav {
    .owl-next,
    .owl-prev {
      margin-top: -78px;
    }
  }
}
