.intro {
  height: 582px;

  background-image: url("../img/intro-bg-flag.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: contain;

  .container {
    height: 100%;
  }

  img {
    display: block;
  }
}

.intro__wrapper {
  padding: 45px;
  height: 100%;

  background-image: url("../img/intro-bg-people.png");
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: contain;
}

.intro__title {
  margin: 0;
  padding-top: 181px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largerFontSize;
  line-height: $largerLineHeght;
  color: $color-blue;
}

.intro__text {
  display: block;
  margin-bottom: 38px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $color-blue;
}

.intro__link {
  margin-top: 21px;
}

// $media-breakpoint-xl = 1199px
@media (max-width: $media-breakpoint-xl) {
  .intro {
    background-size: cover;
  }

  .intro__wrapper {
    padding: 45px 0;
  }

  .intro__title {
    padding-top: 43px;
  }

  .intro__text {
    margin-bottom: 58px;
  }
}

// $media-breakpoint-lg = 991px
@media (max-width: $media-breakpoint-lg) {
  .intro__title {
    padding-top: 32px;
  }
}

// $media-breakpoint-md = 767px
@media (max-width: $media-breakpoint-md) {
  .intro {
    height: 562px;
  }

  .intro__wrapper {
    padding: 24px 0;

    text-align: center;
  }

  .intro__title {
    padding-top: 0;
  }

  .intro__text {
    margin-bottom: 64px;
  }

  .intro__link {
    margin-right: auto;
    margin-left: auto;
  }
}

// $media-breakpoint-sm = 575px
@media (max-width: $media-breakpoint-sm) {
  .intro {
    height: 478px;
  }

  .intro__title {
    margin-bottom: 11px;

    font-size: $largFontSize;
    line-height: $largLineHeght;
  }

  .intro__text {
    margin-bottom: 120px;

    font-size: $baseFontSize - 2;
    line-height: $baseLineHeght + 1;
  }
}
