.news {
  margin: 0;
}

.news__wrapper {
  margin: 0;
  padding: 92px 0 140px;
}

.news__head {
  display: flex;
  justify-content: space-between;

  margin-bottom: 70px;
}

.news__title {
  position: relative;

  margin: 0;
  padding-left: 117px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $color-darker-gray;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 16px;

    width: 100px;
    height: 16px;

    background: url("../img/icon__decor-heading-blue.svg") no-repeat center center;
  }
}

.news__body {
  display: flex;
  justify-content: space-between;
}

.news__own {
  display: flex;
  flex-basis: 100%;

  .news__link {
    display: block;
  }

  img {
    margin-right: 30px;
  }
}

.news__own-body {
  display: flex;
  flex-direction: column;

  max-width: 254px;

  .news__link {
    margin-top: auto;
  }
}

.news__own-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.news__head-link {
  font-size: $baseFontSize - 2;
  line-height: $baseLineHeght + 1;
  letter-spacing: 0.05em;
  color: $color-darker-gray;
}

.news__own-text {
  margin-top: 46px;
}

.news__date {
  font-size: $baseFontSize - 6;
  line-height: $baseLineHeght - 7;
  color: $color-gray;
}

.news__text {
  color: $color-gray;
}

.news__link {
  color: $color-gray;
}

.news__list {
  display: flex;

  margin: 0;
  padding: 0;

  list-style: none;

  img {
    display: block;
  }

  p {
    margin: 0;
  }

  .news__head-link {
    display: inline-block;

    min-height: 46px;
  }

  .news__text {
    margin: 23px 0 32px;
  }
}

.news__item {
  margin-left: 42px;
  width: 50%;
}

.news__item-body {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  .news__date {
    margin-left: 16px;
  }
}

// $media-breakpoint-xl = 1199px
@media (max-width: $media-breakpoint-xl) {
  .news__body {
    justify-content: flex-start;
  }

  .news__own {
    flex-basis: auto;
  }

  .news__list {
    display: block;
    margin-left: 60px;
    max-width: 230px;
  }

  .news__item {
    margin-left: 0;
    width: 100%;

    &:last-child {
      display: none;
    }
  }
}

// $media-breakpoint-lg = 991px
@media (max-width: $media-breakpoint-lg) {
  .news__wrapper {
    padding: 70px 0;
  }

  .news__own {
    .news__link {
      flex-shrink: 0;
    }
  }

  .news__own-body {
    max-width: none;
  }

  .news__list {
    display: none;
  }
}

// $media-breakpoint-md = 767px
@media (max-width: $media-breakpoint-md) {
  .news__wrapper {
    position: relative;

    .button {
      position: absolute;
      right: 0;
      bottom: 70px;
    }
  }

  .news__head {
    margin-bottom: 52px;
  }

  .news__own {
    display: block;

    img {
      margin-right: 0;
      width: 100%;
    }
  }

  .news__own-body {
    flex-grow: 1;
    margin-top: 23px;
  }

  .news__own-text {
    margin: 40px 0;
  }
}

// $media-breakpoint-sm = 575px
@media (max-width: $media-breakpoint-sm) {
  .news__wrapper {
    padding-bottom: 120px;

    .button {
      right: 50%;
      margin-right: -94px;
      bottom: 33px;
    }
  }

  .news {
    .container {
      padding: 0;
    }
  }

  .news__title {
    padding-left: 24px;

    &::before {
      content: none;
    }
  }

  .news__own img {
    height: 210px;
  }

  .news__own-body {
    padding: 0 24px;
  }

  .news__own-text {
    margin: 15px 0 40px;
  }
}
