.about {
  color: $color-blue-gray;

  background: $color-blue;
  background-image: url("../img/about-bg-large.png");
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: contain;

  img {
    display: block;
  }

  p {
    margin: 0;
  }
}

.about__wrapper {
  display: flex;
  justify-content: space-between;

  margin: 0;
  padding: 60px 45px 72px;
}

.about__description {
  margin: 0;
  max-width: 350px;
}

.about__title {
  position: relative;

  margin: 0;
  margin-bottom: 14px;
  padding-left: 117px;

  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $white;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 16px;

    width: 100px;
    height: 16px;

    background: url("../img/icon__decor-heading.svg") no-repeat center center;
  }
}

.about__text {
  line-height: $baseLineHeght - 2;

  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.about__info {
  display: flex;
  justify-content: center;
  align-items: baseline;

  margin: 0;
  margin-top: 32px;
  margin-left: 4px;
  padding: 0;

  line-height: 20px;

  li {
    margin-left: 4%;
    width: 157px;

    text-align: center;
    list-style: none;
  }

  b {
    display: block;

    margin-bottom: 12px;
    width: 153px;
    height: 153px;

    font-size: $largFontSize + 12;
    line-height: 153px;
    color: $white;

    border: 2px solid $color-blue-light;
    border-radius: 50%;
  }
}

// $media-breakpoint-xl = 1199px
@media (max-width: $media-breakpoint-xl) {
  .about {
    background-image: url("../img/about-bg.png");
  }

  .about__wrapper {
    padding: 15px 0 30px;
  }

  .about__description {
    margin-top: 45px;
  }

  .about__info {
    flex-wrap: wrap;
    flex-grow: 1;
    margin-top: 0;

    li {
      margin-left: 14%;
    }
  }
}

// $media-breakpoint-lg = 991px
@media (max-width: $media-breakpoint-lg) {
  .about__wrapper {
    flex-wrap: wrap;
  }

  .about__description {
    flex-grow: 1;
    max-width: none;
  }

  .about__info {
    justify-content: space-between;
    flex-wrap: nowrap;
    flex-grow: 1;
    margin-top: 28px;
    margin-left: 0;

    li {
      margin-left: 0;
    }
  }
}

// $media-breakpoint-md = 767px
@media (max-width: $media-breakpoint-md) {
  .about__info {
    display: none;
  }
}

// $media-breakpoint-sm = 575px
@media (max-width: $media-breakpoint-sm) {
  .about__wrapper {
    padding: 30px 0;
  }

  .about__description {
    margin-top: 0;
  }

  .about__title {
    margin-bottom: 30px;
  }
}
