.nav__list {
  display: flex;

  margin: 0;
  padding: 0;

  list-style: none;

  a {
    font-size: $baseFontSize;
    line-height: 22px;
    text-decoration: none;
    color: $color-blue-gray;

    &:hover {
      text-decoration: underline;
      color: $hover-color;
    }
  }
}

.nav__item {
  &:not(:last-child) {
    margin-right: 5.4%;
  }
}

@media (max-width: $media-breakpoint-lg) {
  .nav__list {
    display: block;
    margin-bottom: 36px;

    a {
      display: block;
      padding: 14px 20px;
    }
  }

  .nav__item {
    &:not(:last-child) {
      margin-right: 0;
    }
  }
}

@media (max-width: $media-breakpoint-md) {
  .page-header__social {
    margin-bottom: 39px;
  }
}
