.page-header {
  position: relative;

  padding: 0;
}

.page-header__top {
  display: flex;
  align-items: center;

  padding: 12px 45px;
}

.page-header__logo {
  margin-right: 20px;
}

.page-header__slogan {
  font-family: "Euclid Flex", "Arial", sans-serif;
  font-size: $baseFontSize - 2;
  line-height: 23px;
  color: $color-dark-gray;
}

.page-header__phone {
  margin-left: auto;

  font-size: $baseFontSize - 4;
  line-height: 17px;
  text-decoration: none;
  color: $color-blue;

  img {
    vertical-align: middle;
    margin-right: 4px;
  }

  &--white {
    display: none;
  }
}

.page-header__bottom {
  height: 70px;

  background-color: $color-blue;
}

.page-header__social {
  display: flex;

  margin: 0;
  padding: 0;

  list-style: none;

  li:not(:first-child) {
    margin-left: 14px;
  }

  img {
    display: block;
  }
}

.page-header__navbar-toggler {
  margin: 0;
}

@media (min-width: 1200px) {
  .page-header__top {
    margin-right: auto;
    margin-left: auto;
    width: 1110px;
  }
}

@media (max-width: $media-breakpoint-xl) {
  .page-header__top {
    padding: 12px 31px;
  }

  .page-header__bottom {
    padding: 0 31px;
  }
}

@media (max-width: $media-breakpoint-lg) {
  .page-header__top {
    padding: 12px 39px;

    border-bottom: 4px solid $color-blue;
  }

  .page-header__phone {
    margin-right: 70px;
  }

  .page-header__bottom {
    padding: 0;
    height: auto;
  }

  .page-header__social {
    margin-left: 13px;

    li:not(:first-child) {
      margin-left: 0;
    }

    a {
      display: block;
      padding: 7px;
    }
  }
}

@media (max-width: $media-breakpoint-md) {
  .page-header__top {
    padding: 12px 33px;
  }

  .page-header__logo {
    margin-right: 14px;
  }

  .page-header__phone {
    display: none;

    &--white {
      display: block;
      margin-right: 0;
      margin-left: 20px;

      color: $white;
    }
  }
}

@media (max-width: $media-breakpoint-sm) {
  .page-header__top {
    padding-right: 16px;
  }

  .page-header__slogan {
    max-width: 180px;
  }
}
